/**
 * The TOC component of the DOCS website
 *
 * Dependencies:
 *     - docs.utilities: Load TOC Json file from the network
 *     - docs.mainframe: Construct TOC DOM elements and manipulate existing page structures
 */


define(["docs.utilities", "docs.mainframe"], function (utilities,  mainframe) {
    "use strict";

    /**
     * Render the in-topic TOC to the current page (will use all h2 element texts).
     * @returns void
     */
    function renderInTopicToc(minItems) {
        var hasItem = false;
        var tocContainer = $("<ol></ol>");
        var docOutlines = $(".doc-outline");
        var tocItems = $("#main h2");

        if(minItems !== undefined && tocItems.length < minItems)
        {
            docOutlines.hide();
            return;
        }
        
        tocItems.each(function () {
            hasItem = true;
			var h2Id = $(this).attr("id");
			if(h2Id === undefined)
			{
				h2Id = $(this).text().toLowerCase().replace(/\s+/g, '_');
				$(this).attr("id", h2Id)
			}

            var item = $("<li></li>");
            var itemLink = $("<a href='#" + h2Id + "'>" + $(this).text() + "</a>");
            item.append(itemLink);
            tocContainer.append(item);
        });
        if (hasItem) {
            docOutlines.find("h3").show();
            docOutlines.append(tocContainer);            
        } else {
            docOutlines.hide();
        }
    }

    function cleanTitle(value) {
        return String(value)
            .replace(/&/g, '&amp;')
            .replace(/"/g, '&quot;')
            .replace(/'/g, '&#39;')
            .replace(/</g, '&lt;')
            .replace(/>/g, '&gt;');
    }

    return {
        renderInTopicToc: renderInTopicToc,
        cleanTitle: cleanTitle
    };
});



