define(['locale', 'docs.utilities', 'cookies'], function(locale, utility, cookies) {
    "use strict";

    var path = utility.getUrlRelativeToThemeBase('templates/uhf.xml');

    var getPrivacyLink = function() {
        return "//privacy.microsoft.com/" + msDocs.data.userLocale + '/';
    }   

    var getRedirectFlag = function() {
        var redirectFlag = msDocs.functions.getParam('redirectedfrom');
        var redirectString = String(redirectFlag);
        redirectString = redirectString.toLowerCase();
        return redirectString;
    }

    var handleRedirectFlag = function() {
        var cookieName = 'hideRedirectMSDN';
        var elementID = '#msdn-redirection-notification';
        var closeButtonID = 'a#redirect-notification-close';
        var redirectFlag = getRedirectFlag();

        if (redirectFlag==='msdn') {
            $(elementID).css('display', cookies.getCookie(cookieName) ? 'none' : 'inline-block');
            $(closeButtonID).click(function() {
                cookies.setCookie(cookieName, 1);
                $(elementID).css('display', 'none');
            });
        }
    }

    var locFile = require('locFile');
    
    locFile.loadUHFFile(function(uhf) {
        var headerLocString = JSON.parse(uhf.find('headerLocJson').text());

        $('#cookie-notification-close').attr('title', headerLocString.close);
        $('#redirect-notification-close').attr('title', headerLocString.close);

        $('#privacystatement-link').text(headerLocString.learnmore)
                                   .attr('title', headerLocString.privacystatement);
        $('#cookie\\.consent\\.europe').text(headerLocString['cookie.consent.europe']);        

        var impressum = require('impressum');

        var sharedLocString = JSON.parse(uhf.find('sharedLocJson').text());

        $('#langDisclaimer').find('span.disclaimertext').text(sharedLocString['disclaimer.text']);
        $('.doc-outline').find('h3').text(sharedLocString['in.this.article']);

        //set filter text
        $('form.tocFilter').find('input').attr('placeholder', sharedLocString['filter.text']);
        $('div.emptyFilterMessage').text(sharedLocString['no.results']);

        //footer
        var $footer = $("#footerPlaceholder");
        $footer.replaceWith(uhf.find('footerHtml').text());
        
        $('#footer-privacy-link').attr('href', getPrivacyLink());
        
        impressum.updateFooter();

        locale.setupLocalePicker();
    });

    jQuery(function($) {
        handleRedirectFlag();
    });
});


