define(function() {
    "use strict";



    function loadUHFFile(doAfter){
        if (!msDocs.data.uhfLoader) {
            var utility = require("docs.utilities");
            var path = utility.getUrlRelativeToThemeBase('templates/uhf.xml');

            msDocs.data.uhfLoader = $.ajax({
                url: path,
                dataType: "xml",
                global: false
            }).done(function(uhfXml) {
                var uhf = $(uhfXml);
                msDocs.data.uhf = uhf;
            });
        }

        msDocs.data.uhfLoader.done(function(uhfXml) {
            if (!msDocs.data.uhf) {
                var uhf = $(uhfXml);
                doAfter(uhf);
            }
            else
            {
                doAfter(msDocs.data.uhf);
            }
        });
    }

    return {
        loadUHFFile: loadUHFFile
    };
});


