define(function () {
    "use strict";

    // setup trace and global error handler at the very beginning
    var trace = require('trace');
    // TODO: figure out a solution so that both consoleTraceListener and insight can receive all traces.
    // Temporarily disable consoleTraceListener, otherwise performance events will be consumed by it and won't be received by insight
    // trace.subscribe(trace.consoleTraceListener);
    var errors = require('errors');

    var cookies = require('cookies');

    var locale = require('locale');

    var _  = require('lodash');

    var headerfooter = require('headerfooter');

    window.NinjaMode = (cookies.getCookie('NinjaMode') === 'true');

    String.prototype.format = String.prototype.f = function () {
        var s = this,
            i = arguments.length;

        while (i--) {
            s = s.replace(new RegExp('\\{' + i + '\\}', 'gm'), arguments[i]);
        }
        return s;
    };


    var layoutConstants = {
        mobileWidthInPixel: 600,
        tabletWidthInPixel: 769,
        desktopWidthInPixel: 980,
        wideScreenWidthInPixel: 1180
    };
    var docs = {
        // build navigation
        buildNav: function (type) {
            var firstNav, secondNav, listOpenEl, listCloseEl;
            if (type === 'mobilenavi') {
                firstNav = '<option value="{0}">{1}</option>';
                secondNav = '<option value="{0}">&nbsp;&nbsp;{1}</option>';
                listOpenEl = "";
                listCloseEl = "";
            }
            else if (type === 'navi') {
                firstNav = '<h3><a href="#{0}">{1}</a></h3>';
                secondNav = '<li class="toc-level-0"><div class="toc-content"><a href="#{0}" title="">{1}</a></div></li>';
                listOpenEl = "<ol>";
                listCloseEl = "</ol>";
            }
            var navigation = firstNav.format('overview', 'Overview');
            var overviewFlag = false;
            var startOpenWritten = false;
            if ($('header#summary').length) {
                overviewFlag = true;
                navigation += listOpenEl + secondNav.format('summary', 'Introduction');
                startOpenWritten = true;
            }
            if ($('header#conceptual').length) {
                overviewFlag = true;
                if (!startOpenWritten)
                {
                    navigation += listOpenEl;
                    startOpenWritten = true;
                }
                navigation += secondNav.format('conceptual', 'Using');
            }
            if ($('header#remarks').length) {
                overviewFlag = true;
                if (!startOpenWritten)
                {
                    navigation += listOpenEl;
                    startOpenWritten = true;
                }
                navigation += secondNav.format('remarks', 'Remarks');
            }
            if (overviewFlag && startOpenWritten) {
                navigation += listCloseEl;
            }
            else {
                navigation = '';
            }
            
            //TODO: localize headerText

            // class block
            if ($('header#constructors').length) {
                navigation = this.buildCodeNav(navigation, 'constructors', 'Constructors', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            if ($('header#fields').length) {
                navigation = this.buildCodeNav(navigation, 'fields', 'Fields', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            if ($('header#properties').length) {
                navigation = this.buildCodeNav(navigation, 'properties', 'Properties', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            if ($('header#methods').length) {
                navigation = this.buildCodeNav(navigation, 'methods', 'Methods', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            if ($('header#events').length) {
                navigation = this.buildCodeNav(navigation, 'events', 'Events', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            if ($('header#operators').length) {
                navigation = this.buildCodeNav(navigation, 'operators', 'Operators', firstNav, secondNav, listOpenEl, listCloseEl, 0);
            }
            // namespace block
            if ($('header#classes').length) {
                navigation = this.buildCodeNav(navigation, 'classes', 'Classes', firstNav, secondNav, listOpenEl, listCloseEl, 1);
            }
            if ($('header#interfaces').length) {
                navigation = this.buildCodeNav(navigation, 'interfaces', 'Interfaces', firstNav, secondNav, listOpenEl, listCloseEl, 1);
            }
            if ($('header#structs').length) {
                navigation = this.buildCodeNav(navigation, 'structs', 'Structs', firstNav, secondNav, listOpenEl, listCloseEl, 1);
            }
            if ($('header#enums').length) {
                navigation = this.buildCodeNav(navigation, 'enums', 'Enums', firstNav, secondNav, listOpenEl, listCloseEl, 1);
            }
            if ($('header#delegates').length) {
                navigation = this.buildCodeNav(navigation, 'delegates', 'Delegates', firstNav, secondNav, listOpenEl, listCloseEl, 1);
            }
            return navigation;
        },

        buildCodeNav: function (navigation, header, headerText, firstNav, secondNav, listOpenEl, listCloseEl, pageType) {
            var parentNav = firstNav.format(header, headerText) + listOpenEl;
            var childNav = '';
            var isEnum = false;
            if ($('header#' + header).siblings('.enumfields').length)
            {
                isEnum = true;
            }
            if (isEnum) {
                $('header#' + header).next().children().filter(':visible').find('td[id]').each(function () {
                    var id = $(this).attr('id');
                    var name = $(this).find('h4 code:visible').text();
                    childNav += secondNav.format(id, _.escape(name));
                });
            }
            else {
                if(pageType === 0)
                {
                    //class
                    $('header#' + header).next().children().filter(':visible').each(function () {
                        var id = $(this).attr('id');
                        var name = $(this).find('h4 code:visible').text();
                        childNav += secondNav.format(id, _.escape(name));
                    });
                } else {
                    //namespace
                    $('header#' + header).next().children().children().filter(':visible').each(function () {
                        var id = $(this).attr('id');
                        var name = $(this).find('code:visible').text();
                        childNav += secondNav.format(id, _.escape(name));
                    });
                }              
            }
            if (childNav.length) {
                $('header#' + header).attr('hidden', false);
                navigation += parentNav + childNav + listCloseEl;
            } else {
                $('header#' + header).attr('hidden', true);
            }
            return navigation;
        }
    };

    var perf = require('perf');
    var perfEvents = require('perf.events');

    $(function () {
        // P1 Contents loaded. For any page, when this function is triggered,
        // DOM should be ready and user should see the text contents with correct style.
        perf.triggerEvent(perfEvents.PERF_EVENT_P1_CONTENT_LOADED);
    });

    return docs;
});






