/**
 * The Personality component of the DOCS website
 */

define(["cookies", "lodash"], function(cookies, _) {
    "use strict";

	function runSelectorFallback($selector){
		$selector.val($($selector.find('option')[0]).val());
	}

    function selectorSettingPersistenceHelper($selector, persistenceKey, workerCallback, initialCallback, defaultValueCallback) {
        if ($selector) {
            $selector.change(function () {
                var persistenceValue = $selector.val();
                workerCallback(persistenceValue);
                cookies.setLocalStorage(persistenceKey, persistenceValue);
            });
            var persistenceValue = cookies.getLocalStorage(persistenceKey);
            if (persistenceValue) {
                $selector.val(persistenceValue);
                if (!$selector.val()) {
					if (defaultValueCallback) {
                		defaultValueCallback($selector);
            		} else {
						runSelectorFallback($selector)
					}                   	
                }
            } else {
				if (defaultValueCallback) {
					defaultValueCallback($selector);
				}
			}
            if (initialCallback) {
                initialCallback($selector.val());
            }
        }
    }

    function replaceOptionsDisplayTextHelper(selector, displayTextMapping) {
        if (selector) {
            selector.find('option').each(function () {
                var originalText = _.trim($(this).text());
                var displayText = displayTextMapping[originalText];
                if (displayText) {
                    $(this).text(displayText);
                }
            });
        }
    }

    function changeProgrammingLanguage(targetLanguage) {
        $('h1 span[class*="lang"]').each(function(){
            $(this).attr('hidden', true);
        });

        $('code[class*="lang"]').parent('pre').attr('hidden',false);

        $('.enable-lang-filter').attr('hidden', true);

        $('code[class*="lang"]').attr('hidden', true);

        $('div[class*="lang"]').attr('hidden', true);

        $('.' + targetLanguage).attr('hidden', false);

        //find all pre tags with no visible elements and hide them
        $('code[class*="lang"]').each(function() {
            if (!$(this).parent('pre').find(':visible').length) {
               $(this).parent('pre').attr('hidden', true);
            }
        });

        $('code[lang=\'' + targetLanguage + '\']').each(function () {			
            $(this).attr('hidden', false);
        });
    }

    function hookProgrammingLanguageSetting() {
		msDocs.data.cookieDevLang = 'proglang456s';
        selectorSettingPersistenceHelper($("#lang-selector"), msDocs.data.cookieDevLang, function (target) {
            changeProgrammingLanguage(target);
        }, function (target) {
            if (target) {
                changeProgrammingLanguage(target);
            }
        }, function ($selector){			
			if(msDocs.settings.defaultDevLang) {
				var langName = "lang-" + msDocs.settings.defaultDevLang;
				$selector.val(langName);
                if (!$selector.val()) {
					runSelectorFallback($selector);                	
                }
			} else {
				runSelectorFallback($selector);
			}
		});
    }

    function changePlatform(targetPlatform) {
        $('.enable-platform-filter').each(function () {
            $(this).attr('hidden', true);
        });
        if (targetPlatform) {
            $('.' + targetPlatform).attr('hidden', false);
        }
    }

    function hookPlatformSetting() {
        var platformSelector = $("#platform-selector");
        replaceOptionsDisplayTextHelper(platformSelector, {
            "net40": ".NET",
            "winform40": ".NET.WindowsForms",
            "wp8": "WinPhone",
            "sl": "WinPhoneSilverlight",
            "rt": "WinRT"
        });
        selectorSettingPersistenceHelper(platformSelector, "platform", function (target) {
            changePlatform(target);
        }, function (target) {
            if (target) {
                changePlatform(target);
            }
        });
    }

    return {
        hookProgrammingLanguageSetting: hookProgrammingLanguageSetting,
        hookPlatformSetting: hookPlatformSetting
    };
});



