/**
 * The Metadata component of the DOCS website
 *
 * Dependencies:
 *
 */

define(function() {
    "use strict";

    function getMetadata(metadataName) {
        var metaElement = $("meta[name=\"" + metadataName + "\"]");
        if (metaElement.length !== 1) {
            return null;
        }
        return metaElement.first().attr("content");
    }

    return {

        get: getMetadata
    };
});



