$(function () {
    "use strict";

    var docs = require("docs");
    var docsToc = require("docs.toc");
    var util = require("util");
    var docsPersonality = require("docs.personality");

     require(["experiment"], function(){
        require("insight");
    });

    require(["docs.config", "docs.branchselector", "rating", "survey"], function (config, docsBranchSel) {

        var docsCode = require("docs.code");
        docsCode.run();

        util.loadLibrary(config.paths.openfeedback);

        // Branch Selector
        docsBranchSel.renderBranchSelectorAsync();

        // Generate QR code of current URL.
        if($("#shareWithQrcode").length) {
            require(["jquery-qrcode"], function () {
                $("#shareWithQrcode").qrcode({
                    text: location.origin + location.pathname,
                    width: 96,
                    height: 96
                });
            });
        } 
    });

    // In topic TOC
    docsToc.renderInTopicToc();

    // Personality settings (programming language, platform)
    docsPersonality.hookProgrammingLanguageSetting();
    docsPersonality.hookPlatformSetting();

    // mobile navi switching
    $('.mobilenavi').change(function () {
        var target = '#' + $('.mobilenavi select').val();
        $(document).scrollTop($(target).offset().top);
        autoExpand(target);
    });

    // build share url
    var shareLink = location.origin + location.pathname;
    var facebookShareUrl = util.buildFacebookShareUrl(shareLink);
    var twitterShareUrl = util.buildTwitterShareUrl(shareLink, document.title);
    $(".share-facebook").attr("href", facebookShareUrl);
    $(".share-twitter").attr("href", twitterShareUrl);

    // Sharing with friends in VK when content locale is ru-ru (Russian).
    var vkShareUrl = util.buildVKShareUrl(shareLink);
    $(".share-vk").attr("href", vkShareUrl);

    // Sharing to weibo and WeChat when content locale is zh-cn (China).
    var weiboShareUrl = util.buildWeiboShareUrl(shareLink, document.title);
    $(".share-weibo").attr("href", weiboShareUrl);

    // share toggling
    $('body').not('.share-container').click(function () {
        $('.share-container').hide();
    });
    $('.sharebutton').click(function () {
        var pos = $('.sharebutton').position();
        $('.share-container').css('left', pos.left);
        $('.share-container').css('top', pos.top + 36);
        $('.share-container').toggle();
        return false;
    });

    // social share window control
    $('.share-container > li > a').click(function () {
        window.open(this.href, '', 'width=600,height=650');
        return false;
    });

    // add BI tag for code block
    $('figure').attr('ms.cmpgrp', 'code');

    // add alert effect to TIP/NOTE/IMPORTANT/WARNING
    $('.TIP, .NOTE, .IMPORTANT, .WARNING').addClass('alert');

    //derived classes
    $('.derivedClassesButton').click(function () {
        var hdc = $('.hiddenDerivedClass[hidden]');
        if(hdc.length)
        {            
            hdc.removeAttr('hidden');            
            $('.derivedClassesButton').html('Less&#8230;')
        }
        else {
            $('.hiddenDerivedClass').attr('hidden', 'true');
            $('.derivedClassesButton').html('More&#8230;')
        }
        return false;
    });
});


